import { FORMAT_PAINTER_COPY, FORMAT_PAINTER_CLEAR_CLIPBOARD } from '../actions';
import copyFormat from '../commands/copyFormat';
import clearFormatClipboard from '../commands/clearFormatClipboard';

export default function onAction(action, editor, next) {
  const { type, payload } = action;

  if (type === FORMAT_PAINTER_COPY) {
    const { copyType, defaultSz } = payload;
    return copyFormat(editor, 'toolbar', copyType, defaultSz);
  }
  if (type === FORMAT_PAINTER_CLEAR_CLIPBOARD) {
    return clearFormatClipboard(editor);
  }

  return next();
}
